
import "./gallerys.scss";
import ImageGallery from 'react-image-gallery';
import "react-image-gallery/styles/scss/image-gallery.scss";
import { useState } from "react";
import ImageGrid from "../imageGrid/indexGrid";

function Gallery({ images }) {

    const [galleryActive, setGalleryActive] = useState(false);
    const [currentImage, setCurrentImage] = useState(0);

    const openGallery = (event) => {
        setGalleryActive(true);
        setCurrentImage(+event.target.dataset.id);
    }

    const closeGallery = () => {
        setGalleryActive(false);
        setCurrentImage(0);
    }


    return (
        <section className='gallery_section'>
            <div className="container">

                <h2 className="lugrissimo with_lines">My gallery</h2>
                <p className="gallery_section_text">Photos taked on my phone</p>
                {/* {
                    !galleryActive ? <ImageGrid
                        colsCount={8}
                        images={images}
                        click={openGallery}
                    /> : ''
                } */}

                {/* {
                    galleryActive ? (<div className="gallery_content">
                        <button className="close_gallery" onClick={closeGallery}>
                            <img src="/images/close-icon.svg" alt="close-icon" />
                        </button>
                        <ImageGallery
                            items={images}
                            startIndex={currentImage || 0}
                        />
                    </div>) : ''
                } */}

              
                     <div className="gallery_content">
                        <ImageGallery
                            items={images}
                            startIndex={currentImage || 0}
                        />
                    </div>
                
            </div>
        </section>
    )
}

export default Gallery;